import React, { useEffect, useState, useRef } from "react";
import Cookies from "js-cookie";
import axios from "axios";
import { useParams } from "react-router-dom";
import styles from "../assets/styles/Results.module.css";
import Diff from "./Diff";

const WordsResults = () => {
  const params = useParams();
  const id = params.id;
  const user_id = params.user_id;
  const [results, setResults] = useState();
  const score = useRef(0)
  useEffect(() => {
    if (results == undefined || results == null) getResults();
  });

  const getResults = async () => {
    console.log(results);
    const config = {
      headers: {
        "Content-Type": "application/json",
        "Authorization": `JWT ${localStorage.getItem("access")}`,
        "x-csrftoken": Cookies.get("csrftoken"),
      },
    };
    try {
      const response = await axios.get(
        process.env.REACT_APP_API_URL + `/get_words_results/${user_id}/${id}/`,
        config
      );
      response.data[0].words = response.data[0].words.split(",");
      response.data[0].spoken_words = response.data[0].spoken_words.split(",");
      for(let i = 0;i<response.data[0].words.length;i++){
        if(response.data[0].words[i] === response.data[0].spoken_words[i]){
            score.current = score.current+1;
        }
      }
      console.log(score.current)
      console.log(response.data[0]);
      setResults(response.data[0]);
    } catch (err) {
      console.log(err);
    }
  };

  return (
    <div className={styles.testDiv}>
      <h1 className={styles.testTitle}>Sentence Test</h1>
      <h2 className={styles.testTitle}>
        Score: {score ? score.current/2 : null}/{results ? results.words.length : null}
      </h2>
      {results !== null && results !== undefined ? (
        <table className={styles.resultsTable} cellSpacing="0" cellPadding="0">
          <thead>
            <tr>
              <th>Sr No.</th>
              <th>Acutal Word</th>
              <th>Spoken Word</th>
              <th>Score</th>
            </tr>
          </thead>
          <tbody>
            {results.spoken_words.map((value, index) => (
              <tr
                key={index}
                style={
                  value !== results.words[index]
                    ? {
                        backgroundColor: "rgba(249, 97, 77, 0.671)",
                        textAlign: "center",
                      }
                    : { textAlign: "center" }
                }
              >
                <td>{index + 1}.</td>
                <td>{results.words[index].toUpperCase()}</td>
                <td>{value === "-1" ? "null" : value.toUpperCase()}</td>
                <td>{value === results.words[index] ? "1" : "0"}</td>
              </tr>
            ))}
          </tbody>
        </table>
      ) : null}
    </div>
  );
};

export default WordsResults;
